@use '../abstracts/constants';

.garage-page-menu {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 350px;
    padding: 10px;
    border-radius: 5px;
    background-color: constants.$color-garage-form-background;
}

.garage-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.create-car-row,
.update-car-row {
    display: flex;
    gap: 5px;
    align-items: stretch;
    justify-content: space-between;
}

.garage-page-top-buttons {
    display: flex;
    gap: 10px;
    justify-content: flex-start;
}

.garage-page-bottom-buttons {
    display: flex;
    gap: 10px;
    justify-content: space-between;
}

.garage-page-top-buttons button {
    width: 100px;
    padding: 6px 12px;
    border: none;
    border-radius: 5px;
    font-family: Roboto, sans-serif;
    background-color: constants.$color-top-garage-buttons;
}

.garage-page-bottom-buttons button {
    padding: 6px 12px;
    border: none;
    border-radius: 5px;
    font-family: Roboto, sans-serif;
    background-color: constants.$color-garage-form-buttons;
}

.create-car,
.update-car {
    width: 65px;
    border: none;
    border-radius: 5px;
    font-family: Roboto, sans-serif;
    background-color: constants.$color-garage-form-buttons;
}

.create-car-input,
.update-car-input {
    width: 60%;
    border: none;
    border-radius: 5px;
    font-family: Roboto, sans-serif;
}

.create-car-color,
.update-car-color {
    border: none;
    border-radius: 5px;
}
