.dark-view {
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.message-container {
    position: relative;
    width: 450px;
    margin: 0 auto;
    padding: 20px;
}

.message {
    position: relative;
    z-index: 3;
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 25px;
    font-size: 30px;
    text-shadow: #529eff 3px 2px 15px;
}
